import { FETCH_REQUEST, FETCH_SUCCESS } from "../actions";

const initialState = {
  loading: false,
  couponResponse:{
    message:'',
    status:''
  },
};

const reducer = (state = initialState, action) => {
  console.log("reducer ",action.type);

  switch (action.type) {
    case FETCH_REQUEST:
      return {
        ...state,
        loading: true,
      };
    case FETCH_SUCCESS:
      return {
        ...state,
        couponResponse: action.payload,
        loading: false,
      };
    default:
      return state;
  }
};

export default reducer